.menu-collapse-overlay{
    display:none;
    z-index:5;
    position:fixed;
    background-color:#000;
    width:100%;
    height:100%;
    top:0;
    left:0;
    opacity:.5;
    -webkit-backdrop-filter:blur(3px);
    backdrop-filter:blur(3px)
}
#header{
    z-index:1500;
    width:100%;
    position:relative
}
#header nav{
    margin:0
}
#header nav .region-header:after{
    content:"";
    border-top:1px solid #fff;
    position:absolute;
    width:100%;
    left:0;
    right:0
}
#header nav .region-header>*:last-child{
    float:none
}
#header nav .navbar-header #header-logo{
    background:url("../images/logo.production.png");
    background-size:140px 24px;
    background-repeat:no-repeat;
    background-position:center;
    float:left;
    width:140px;
    height:48px;
    margin:0 15px 0 0
}
#header nav .navbar-header #header-logo.development{
    background:url("../images/logo.development.png");
    background-size:140px 24px;
    background-repeat:no-repeat;
    background-position:center
}
#header nav .navbar-header #header-logo.staging{
    background:url("../images/logo.staging.png");
    background-size:140px 24px;
    background-repeat:no-repeat;
    background-position:center
}
#header nav .navbar-header #header-logo.preproduction{
    background:url("../images/logo.preproduction.png");
    background-size:140px 24px;
    background-repeat:no-repeat;
    background-position:center
}
@media (max-width: 767px){
    #header nav .navbar-header #header-logo{
        margin-left:15px
    }
}
#header nav .navbar-header .navbar-toggle{
    padding:0;
    height:48px;
    width:48px;
    font-size:24px;
    color:#fff;
    margin:0;
    -webkit-transition:all .125s ease-in-out;
    -moz-transition:all .125s ease-in-out;
    -o-transition:all .125s ease-in-out;
    transition:all .125s ease-in-out
}
#header nav .navbar-header .navbar-toggle:hover,#header nav .navbar-header .navbar-toggle:focus,#header nav .navbar-header .navbar-toggle:active{
    background-color:transparent
}
@media (min-width: 768px){
    #header nav .navbar-header .header-menu{
        padding:0
    }
}
#header nav hr{
    clear:both;
    margin:0 -1000%;
    color:#fff;
    border-color:#fff
}
#header nav .menu-name-menu-language ul{
    float:right;
    margin-right:5px;
    font-size:14px
}
#header nav .menu-name-menu-language ul li{
    display:inline-block
}
#header nav .menu-name-menu-language ul li a{
    padding:12px 10px;
    color:#fff !important
}
#header nav .menu-name-menu-language ul li a:active,#header nav .menu-name-menu-language ul li a:focus,#header nav .menu-name-menu-language ul li a:hover{
    background-color:transparent;
    text-decoration:underline
}
#header nav .language-en .en,#header nav .language-fr .fr,#header nav .language-de .de{
    border-top:2px solid #fff;
    font-weight:600
}
#header nav #navbar-user ul li i{
    margin-right:8px
}
#header nav #navbar-user ul li a{
    color:#fff;
    border:none;
    background-color:transparent;
    font-size:14px;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
    max-width:200px
}
#header nav #navbar-user ul li a:active,#header nav #navbar-user ul li a:focus,#header nav #navbar-user ul li a:hover{
    background-color:transparent;
    text-decoration:underline
}
@media (min-width: 992px) and (max-width: 1199px){
    #header nav #navbar-user ul li a{
        max-width:300px
    }
}
@media (min-width: 1200px){
    #header nav #navbar-user ul li a{
        max-width:420px
    }
}
@media (min-width: 768px){
    #header nav #navbar-user ul li .navbar-btn{
        height:48px
    }
}
#header nav #navbar-user ul li .btn-group>a{
    padding:14px 15px;
    font-size:14px
}
#header nav #navbar-user ul li .btn-group>a[aria-expanded="true"]{
    color:#333;
    background-color:#fff
}
#header nav #navbar-user ul li .btn-group .dropdown-menu{
    margin:0
}
#header nav #navbar-user ul li .btn-group .dropdown-menu li a{
    color:#333
}
@media (max-width: 767px){
    #header nav #navbar-user ul li .btn-group{
        width:100%
    }
    #header nav #navbar-user ul li .btn-group .dropdown-menu{
        margin-top:0;
        width:100%;
        display:block;
        position:static;
        float:none;
        background-color:transparent
    }
    #header nav #navbar-user ul li .btn-group .dropdown-menu li a{
        padding:10px 0;
        color:#fff !important;
        max-width:100%
    }
    #header nav #navbar-user ul li .btn-group .dropdown-menu li a:active,#header nav #navbar-user ul li .btn-group .dropdown-menu li a:focus,#header nav #navbar-user ul li .btn-group .dropdown-menu li a:hover{
        background-color:transparent;
        text-decoration:underline
    }
    #header nav #navbar-user ul li .btn-group .divider{
        border-color:#fff
    }
}
@media (min-width: 768px){
    #header nav #navbar-user{
        padding-left:0
